@keyframes fade {
    0% {
        opacity: 0.3;
    }
    100% {
        opacity: 1;
    }
}

.contextMenuBg {
    display: none;
    position: absolute;
    top: var(--menu-height);
    overflow: hidden;
    animation: fade 0.2s;
    background-color: var(--menu);
    color: var(--menu-text);
    cursor: default;
    box-shadow: var(--border-window-outer), var(--border-window-inner), 5px 5px 4px -4px rgba(0, 0, 0, 0.75);
    box-sizing: content-box;
}

.contextMenuBg u {
    border-bottom-color: var(--underline);
}

.contextMenuBg:focus {
    outline: none;
}

.contextMenu {
    position: absolute;
    right: 5px;
    bottom: 3px;
    margin: 0 !important;
    margin-right: 3px;
}

.contextMenuItem {
    width: 100%;
    height: calc(var(--menu-height) - 1px);
    font-size: 11px;
    padding-left: 2px;
    position: relative;
    font: var(--menu-font);
    line-height: unset;
}

.contextMenuItem p {
    margin: 0;
    padding-top: 2px;
}

.contextMenuItem > p {
    padding-left: 20px;
}

.contextMenuItem[data-active] {
    background-color: var(--hilight);
    color: var(--hilight-text);
}

.contextMenuItem[data-active] u {
    border-bottom-color: var(--underline-hilight);
}

.contextMenuItem[data-hidden] {
    display: none;
}

.contextMenu hr {
    width: 100%;
    margin-top: calc(var(--menu-height) / 4 - 1.5px);
    margin-left: 1px;
    margin-right: 0;
    margin-bottom: calc(var(--menu-height) / 4 - 0.5px);
    border: none;
    border-top: solid 1px var(--button-shadow);
    border-bottom: solid 1px var(--button-hilight);
}

.submenuMark {
    position: absolute;
    top: 50%;
    right: -1px;
    transform: translateY(calc(-50% - 4px));
}

.disabled,
[data-disabled] {
    color: var(--button-shadow) !important;
    text-shadow: 1px 1px 0 var(--button-hilight) !important;
}

.disabled u,
button[disabled] u,
[data-disabled] u {
    border-bottom-color: var(--underline-disabled);
}

:root {
  --menu-padding: 3px;
  --item-offset-left: 30px;
  --item-hover-background: linear-gradient(
    to bottom,
    #fff9,
    #e6ecf5cc 90%,
    #fffc
  );
  --item-hover-background-disabled: linear-gradient(
    to bottom,
    #fff9,
    #e6e6e6cc 90%,
    #fffc
  );
}

ul {
  &[role] {
    margin: 0;
    padding: 0;
    list-style: none;
    cursor: default;
  }

  &[role="menu"] {
    min-width: 150px;
    padding: 2px;
    background: var(--surface);
    color: initial;
    border: 1px solid rgba(0, 0, 0, 0.4);
    box-shadow: 4px 4px 3px -2px rgba(0, 0, 0, 0.5);
    border-radius: 0;
    overflow-x: hidden;

    &::before {
      content: "";
      pointer-events: none;
      position: absolute;
      left: var(--item-offset-left);
      width: 2px;
      height: calc(100% - 4px);
      box-shadow: inset 1px 0 rgba(0, 0, 0, 0.15), inset -1px 0 #fff;
    }

    [role="menuitem"] & {
      display: none;
      position: absolute;
      left: 0;
      top: 100%;
      z-index: 99;
    }

    & [role="menuitem"] > [role="menu"] {
      top: -4px;
      left: 100%;
    }

    > [role="menuitem"],
    .main-contextMenu-menuItem {
      .main-contextMenu-menuItemButton {
        height: 24px;

        span {
          margin-left: 21px;
          line-height: 1.1;
          overflow: visible;
        }
      }

      > a,
      > button,
      > label,
      &[aria-haspopup="true"] {
        all: unset;
        position: relative;
        display: flex;
        justify-content: space-between;
        width: 100%;
        padding: 4px 10px 4px 10px;
        box-sizing: border-box;
        border: 1px solid transparent;
        border-radius: 3px;
        white-space: nowrap;

        &:hover,
        &:focus-visible {
          background: var(--item-hover-background);
          border-color: #b8d6fb;
        }

        span {
          font: var(--menu-font);
        }
      }

      > button {
        &:hover::before,
        &::after {
          content: none;
        }
      }

      &[aria-haspopup="true"] {
        &::after {
          content: "";
          position: absolute;
          top: 50%;
          right: 2px;
          transform: translateY(-50%);
          border: 4px solid transparent;
          border-left-color: currentColor;
        }
      }

      svg {
        position: absolute;
        width: 12px;
        left: 6px;

        span:not(:first-child) &.Svg-img-icon-small { /* submenu mark */
          left: auto;
          right: 6px;
        }
      }
    }
  }
}

.contextMenuBg {
  margin: 0;
  padding: 0;
  list-style: none;
  padding: var(--menu-padding) !important;
  background: var(--surface);
  color: initial;
  box-shadow: #979797 -1px -1px inset, #979797 1px 1px inset, #f5f5f5 -3px -3px inset, #f5f5f5 3px 3px inset, 5px 5px 4px -4px rgba(0, 0, 0, 0.75);
  z-index: 1;

  hr {
    border-top-color: #ccc;
    border-bottom-color: #fff;
    margin-left: 29px;
    width: calc(100% - 27px);
  }
}

.contextMenu::before {
  content: "";
  pointer-events: none;
  position: absolute;
  left: calc(var(--item-offset-left) - 3px);
  width: 2px;
  height: 100%;
  box-shadow: inset 1px 0 rgba(0, 0, 0, 0.15), inset -1px 0 #fff;
}

.contextMenuItem {
  position: relative;
  width: calc(100% + 2px);
  height: 24px !important;
  padding: 2px 10px 4px 12px;
  box-sizing: border-box;
  border: 1px solid transparent;
  border-radius: 3px;
  white-space: nowrap;

  &[data-active] {
    background-color: unset !important;
    background: var(--item-hover-background);
    border-color: #b8d6fb;
    box-shadow: none !important;
    color: #000 !important;
  }

  &.disabled {
    color: #6d6d6d !important;
    text-shadow: none !important;
    &[data-active] {
      background: var(--item-hover-background-disabled);
      border-color: #d4d3d3;
    }
  }

  p {
    padding-top: 0 !important;
    padding-left: 20px !important;
    &::before {
      display: none;
    }
  }

  .submenuMark {
    transform: translateY(calc(-50% - 2px));
  }

  &::after {
    all: unset; /* unset the standalone checkbox/radio styles */
    background: var(--item-hover-background);
    box-shadow: 0 0 0 1px #b3d3f9;
    border-radius: inherit;
    box-sizing: border-box;
    position: absolute;
    top: 0;
    left: 0;
    width: 22px;
    height: 22px;
  }
  &::before {
    position: absolute;
    top: 50%;
    right: 2px;
    transform: translateY(-50%);
    z-index: 2;
  }

  &.activeStyle,
  &.checkedItem {
    &::after {
      content: "";
    }
  }
  &.activeStyle::before {
    content: "";
    left: 8px;
    top: 50%;
    transform: translateY(-50%);
    background: radial-gradient(circle at 75% 25%, #d5d4ea, #333583);
    border: 1px solid #1a1490;
    box-shadow: none;
    border-radius: 50%;
    box-sizing: border-box;
    height: 8px;
    width: 8px;
  }
  &.checkedItem::before {
    content: "\2714";
    color: rgb(12, 18, 161);
    font-size: 10pt;
    left: 6px;
    top: 50%;
    transform: translateY(-52%);
    display: block;
    font-weight: 700;
    position: absolute;
  }
}

body:not([data-no-underline]) .contextMenuItem u {
  border-bottom-color: black !important;
}